import { isArray, makeSureArray, ArrayCopy } from './utils';

/**
 * RNode
 * @constructor
 * @param {String} value
 *
 * path:          åŒºåˆ†åŒçº§èŠ‚ç‚¹çš„å”¯ä¸€æ ‡è¯†
 * params:        path åŒ…å«çš„å‚æ•°ï¼Œä½¿ç”¨{å‚æ•°å:å‚æ•°è§„åˆ™}é”®å€¼å¯¹è¡¨ç¤º
 * callbacks:     è·¯ç”±åŒ¹é…æ—¶æ‰§è¡Œçš„å›è°ƒå‡½æ•°æˆ–é˜Ÿåˆ—
 * beforeEnter:   è·¯ç”±åŒ¹é…æ—¶ï¼Œcallbacks æ‰§è¡Œä¹‹å‰æ‰§è¡Œçš„å›è°ƒå‡½æ•°æˆ–é˜Ÿåˆ—ï¼ˆå¦‚æœ beforeEnter è¿”å› false åˆ™ä¸ä¼šè¿›å…¥ callbacks æ‰§è¡Œé˜¶æ®µï¼‰
 * beforeLeave:   è·¯ç”±åŒ¹é…æ—¶ï¼Œè¿›å…¥ä¸‹ä¸€ä¸ªè·¯ç”±ä¹‹å‰ï¼ˆä¹Ÿå°±æ˜¯å½“å‰è·¯ç”±ç¦»å¼€ä¹‹å‰ï¼‰æ‰§è¡Œçš„å›è°ƒå‡½æ•°æˆ–é˜Ÿåˆ—
 * children:      å­èŠ‚ç‚¹åˆ—è¡¨å¼•ç”¨
 * parent:        çˆ¶èŠ‚ç‚¹å¼•ç”¨
 */
function RNode(value) {
  this.path = value;
  this.params = false;
  this.data = null;
  this._hooks = {};
  this.children = [];
  this.parent = null;
}

const proto = RNode.prototype;

proto.callHooks = function _callHooks (hookName, Req) {
  const callbacks = this._hooks[hookName] || [];
  const _copyCallbacks = ArrayCopy(callbacks); // å¤åˆ¶ä¸€ä¸ªï¼Œé¿å…ä¸­é—´è°ƒç”¨äº† off å¯¼è‡´ length å˜åŒ–
  for (let i = 0; i < _copyCallbacks.length; ++i) {
    const previousCallbackReturnValue = _copyCallbacks[i].call(null, Req);
    if (previousCallbackReturnValue === false) break;
  }
  return this;
};

proto.addHooks = function addHooks (hookName, callbacks) {
  this._hooks[hookName] = makeSureArray(callbacks);
  return this;
};

// add children
proto.addChildren = function addChildren (children) {
  if (isArray(children)) {
    this.children = this.children.concat(children);
  } else {
    this.children.push(children);
  }
  return this;
};

proto.removeChild = function removeChild (child) {
  for (let i = 0; i < this.children.length; ++i) {
    if (this.children[i] === child) {
      this.children.splice(i, 1);
      break;
    }
  }
  return this;
};

export default function createRNode (value) {
  return new RNode(value);
}
