import createRNode from './rnode';
import { isArray, makeSureArray, warn } from './utils';

/**
 * æ ¹æ®ç»™å®šçš„ pathï¼Œä»¥ routeTreeRoot ä¸ºæ ¹èŠ‚ç‚¹æŸ¥æ‰¾ï¼Œè¿”å› path å¯¹åº”çš„ rnode èŠ‚ç‚¹
 * å¦‚æœèŠ‚ç‚¹ä¸å­˜åœ¨ï¼Œå¹¶ä¸” createIfNotFound ä¸º true å°±åˆ›å»ºæ–°èŠ‚ç‚¹
 * åŒ¹é…å‚æ•°ï¼ˆå‚æ•°åç”±å­—æ¯ã€æ•°å­—ã€ä¸‹åˆ’çº¿ç»„æˆï¼Œä¸èƒ½ä»¥æ•°å­—å¼€å¤´ã€‚åé¢å¸¦æ‹¬å·çš„æ˜¯ç‰¹å®šå‚æ•°çš„åŒ¹é…è§„åˆ™ã€‚ï¼‰
 * @param {RNode} tree
 * @param {String} path
 * @param {Boolean} createIfNotFound å½“èŠ‚ç‚¹ä¸å­˜åœ¨æ—¶åˆ›å»ºæ–°èŠ‚ç‚¹
 * @return {RNode}
 * */
export function findNode(routeTreeRoot, routePath, createIfNotFound) {
  if (routePath === '') { // å½“å‰èŠ‚ç‚¹
    return routeTreeRoot;
  }
  const parts = routePath.split('/');
  let target = null, found = false;
  let parent = routeTreeRoot;
  let params;
  for (let i = 0, len = parts.length; i < len; ++i) {
    params = false;
    let realCurrentValue = parts[i];

    const matcher = /:([a-zA-Z_][a-zA-Z0-9_]*)(\([^\)]+\))?/g;

    let k = 0;

    function replacement($1, $2, $3) {
      params = params || [];
      params[k++] = $2;
      if (!$3) { // In IE 8 , $3 is an empty String while in other browser it is undefined.
        return '([a-zA-Z0-9_]+)';
      } else {
        return $3;
      }
    }

    realCurrentValue = realCurrentValue.replace(matcher, replacement);

    for (let j = 0; j < parent.children.length; ++j ) {
      if (parent.children[j].path === realCurrentValue) {
        target = parent.children[j];
        found = true;
        break;
      }
    }
    if (!found) { // ä¸å­˜åœ¨
      if (!createIfNotFound) return false;
      // åˆ›å»ºæ–°èŠ‚ç‚¹
      const extendNode = createRNode(realCurrentValue);
      parent.addChildren(extendNode);
      extendNode.parent = parent;
      extendNode.params = params;
      target = extendNode;
    }
    parent = target;
    found = false;
  }
  return target;
}

function createRouteNodeInPath (rootNode, routePath) {
  routePath = routePath.replace(/^\/([^\/]*)/, '$1'); // å»æ‰å‰ç½® /
  if (routePath === '*') {
    const rnode = createRNode('.*');
    rnode.parent = rootNode;
    rootNode.addChildren(rnode);
    return rnode;
  } else {
    return findNode(rootNode, routePath, true);
  }
}

// æ„é€ è·¯ç”±æ ‘
export function createRouteTree(namedRoutes, routeNode, routeOptions) {
  if (routeOptions.name) {
    if (namedRoutes[routeOptions.name]) {
      warn(`å·²ç»å­˜åœ¨çš„å…·åè·¯ç”± ${routeOptions.name} å°†è¢«è¦†ç›–`);
    }
    namedRoutes[routeOptions.name] = routeNode;
  }
  if (routeOptions.data) {
    routeNode.data = routeOptions.data;
  }
  routeNode.addHooks('beforeEnter', routeOptions.beforeEnter);
  routeNode.addHooks('callbacks', routeOptions.controllers);
  routeNode.addHooks('beforeLeave', routeOptions.beforeLeave);
  if (routeOptions.sub) { // å­è·¯ç”±
    for (let subRoutePath in routeOptions.sub) {
      if (routeOptions.sub.hasOwnProperty(subRoutePath)) {
        const subRouteNode = createRouteNodeInPath(routeNode, subRoutePath);
        createRouteTree(namedRoutes, subRouteNode, routeOptions.sub[subRoutePath]);
      }
    }
  }

}

// åˆ›å»ºæ ¹ç»“ç‚¹
export function createRootRouteTree (namedRoutes, routes) {
  const rootRouteNode = createRNode('');
  createRouteTree(namedRoutes, rootRouteNode, {
    sub: routes
  });
  return rootRouteNode;
}

/**
 * @param {RNode} currentRouteNode å½“å‰èŠ‚ç‚¹
 * @param {Array} parts è·¯å¾„åˆ†æ®µæ•°ç»„
 * @param {Integer} ci å½“å‰è·¯å¾„åˆ†æ®µç´¢å¼•
 * @param {Integer} ri å½“å‰èŠ‚ç‚¹æ‰€åœ¨å…„å¼ŸèŠ‚ç‚¹åˆ—è¡¨çš„ä½ç½®
 * @params {Object} params è®°å½•å‚æ•°çš„å¯¹è±¡
 * @return {[RNode, Object]} åŒæ—¶è¿”å›èŠ‚ç‚¹å’Œå‚æ•°
 */
export function dfs(currentRouteNode, parts, ci, ri, params) {

  const value = parts[ci];

  var newParams = {};
  for (var p in params) { // copy: params => newParams
    if (params.hasOwnProperty(p)) {
      newParams[p] = params[p];
    }
  }

  var parent = currentRouteNode.parent;

  if (parent === null && ri > 0) { // finally not matched
    return [false, newParams];
  }

  if (parent !== null && ri > parent.children.length-1) { // not matched, go back
    return [false, newParams];
  }

  if (ci > parts.length-1 || ci < 0) return [false, newParams];

  const matcher = new RegExp('^' + currentRouteNode.path + '$');
  let matches = value.match(matcher);

  if (matches === null) return [false, newParams]; // not matched, go back

  if (!!currentRouteNode.params) {
    matches = [].slice.apply(matches, [1]);
    for (let k = 0; k < matches.length; ++k) {
      newParams[currentRouteNode.params[k]] = matches[k];
    }
  }

  if (ci === parts.length-1 && currentRouteNode.callbacks !== null) { // finally matched
    return [currentRouteNode, newParams];
  }

  for (let i = 0; i < currentRouteNode.children.length; ++i) {
    const found = dfs(currentRouteNode.children[i], parts, ci+1, i, newParams); // matched, go ahead
    if (!found[0]) continue;
    return found;
  }

  return dfs(currentRouteNode, parts, ci, ri+1, params); // not matched, go back
}

/**
 * æœç´¢è·¯ç”±æ ‘ï¼Œçœ‹æ˜¯å¦å­˜åœ¨åŒ¹é…çš„è·¯å¾„ï¼Œå¦‚æœå­˜åœ¨ï¼Œè¿”å›ç›¸åº”çš„å›è°ƒå‡½æ•°
 * @todo åªè¿”å›ç¬¬ä¸€ä¸ªåŒ¹é…åˆ°çš„è·¯ç”±ï¼ˆå¦‚æœå­˜åœ¨å¤šä¸ªåŒ¹é…ï¼Ÿï¼‰
 * @param {RNode} tree æ ‘æ ¹
 * @param {String} path è¦åŒ¹é…çš„è·¯å¾„
 * è¿”å›å€¼åŒ…å«ä¸¤ä¸ªï¼Œç”¨æ•°ç»„è¡¨ç¤º[rnode, params]
 * @return {Function|Array|null} å¦‚æœå­˜åœ¨å°±è¿”å›ç›¸åº”çš„å›è°ƒï¼Œå¦åˆ™è¿”å›null
 * @return {[Array, Object]} åŒæ—¶è¿”å›å›è°ƒå’Œå‚æ•°
 *
 * */
export function searchRouteTree(tree, path) {

  const result = dfs(tree, path.split('/'), 0, 0, {});

  if (!result[0]) {
    let resultNode = null;
    for (let i = 0; i < tree.children.length; ++i) {
      if (tree.children[i].path === '.*') {
        resultNode = tree.children[i];
      }
    }
    return [resultNode, {}];
  }

  return [result[0], result[1]];
}
